/* Asegurarse de que la App esté centrada y no se desborde */
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  overflow-x: hidden; /* Oculta el desbordamiento horizontal */
}

.App-header {
  background-color: red;
  width: 100%; /* Ocupar todo el ancho de la pantalla */
  max-width: 100vw; /* Evitar que el contenido exceda el ancho de la ventana */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: center;
  overflow: hidden; /* Prevenir cualquier desbordamiento */
}

.App-link {
  color: white;
}

/* Opcional: Si hay imágenes u otros elementos que exceden el ancho, limitarlo */
img, video, iframe {
  max-width: 100%;
  height: auto;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
