/* header.css */

/* Ajustes para evitar que el contenido se superponga al header */
main {
  padding-top: 60px; /* Ajusta este valor a la altura exacta de tu header */
}

.header {
  background-color: #333; /* Color de fondo */
  color: white; /* Color del texto */
  padding: 10px; /* Espaciado interno */
  display: flex; /* Usar Flexbox */
  align-items: center; /* Centrar verticalmente */
  width: 100%; /* Asegurarse de que ocupa todo el ancho */
  position: top; /* Fijar el header en la parte superior */
  top: 0; /* Alinear al borde superior */
  left: 0; /* Alinear al borde izquierdo */
  z-index: 1000; /* Asegurarse de que esté por encima de otros elementos */
  justify-content: space-between; /* Espaciar entre el contenido */

}

.header h1, .header h2 {
  margin: 0; /* Sin márgenes */
  font-size: 2rem; /* Tamaño de fuente para el nombre */
  display: inline; /* Mostrar en línea */

}

.header h2 {
  font-size: 1.5rem; /* Tamaño de fuente para el título */ 
  margin-left: 5px; /* Espacio entre el h1 y el h2 */

}



.nav-list {
  list-style-type: none; /* Sin viñetas */
  padding: 0; /* Sin padding */
  margin: 0; /* Sin margen */
  
}

.nav-list li {
  display: inline; /* Mostrar los elementos en línea */
  margin: 0 15px; /* Espaciado entre los elementos del menú */
}

.nav-list a {
  color: white; /* Color del texto del enlace */
  text-decoration: none; /* Sin subrayado */
  font-weight: bold; /* Texto en negrita */
}

.nav-list a:hover {
  text-decoration: underline; /* Subrayar al pasar el ratón */
}
