.contact {
    background-color: #333; /* Color de fondo */
    color: white; /* Color del texto */
    text-align: center; /* Centrar el texto */
    padding: 20px; /* Espaciado interno */
    position: relative; /* Permitir que se coloque correctamente en la parte inferior */
    bottom: 0; /* Alinear al borde inferior */
    width: 100%; /* Asegurarse de que ocupe todo el ancho */
}

.social-links {
    list-style: none; /* Sin viñetas */
    padding: 0; /* Sin padding */
    margin: 0; /* Sin margen */
    display: flex; /* Usar flexbox para los enlaces */
    justify-content: center; /* Centrar los enlaces */
}

.social-links li {
    margin: 0 15px; /* Espaciado entre los enlaces */
}

.social-links a {
    color: white; /* Color de los enlaces */
    text-decoration: none; /* Sin subrayado */
    transition: color 0.3s; /* Transición para el efecto hover */
}

.social-links a:hover {
    color: #00aaff; /* Cambiar color al pasar el ratón */
}
