/* Education.css */
.education-container {
  background-color: #f4f4f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 800px;
  margin: 20px auto; /* Centrar el contenedor */
  display: flex; /* Utiliza flexbox */
  align-items: center; /* Centra verticalmente los elementos */
}

.education-info {
  text-align: left;
  flex: 1; /* Ocupa el espacio restante */
}

.education-logo {
  flex: 0 0 150px; /* Espacio fijo para el logo */
  display: flex; /* Flexbox para centrar la imagen */
  justify-content: center; /* Centrar horizontalmente */
}

.education-logo img {
  max-width: 100%; /* Asegura que la imagen no sobrepase su contenedor */
  height: auto; /* Mantiene la proporción de la imagen */
}

.education-info h2 {
  font-size: 2rem;
  color: #333;
  margin-bottom: 15px;
}

.education-info p {
  font-size: 1.1rem;
  color: #555;
  line-height: 1.6;
}

.education-info strong {
  color: #000;
}
