/* Experience.css */
.experience-container {
    background-color: #f1f1f1;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 800px;
    margin: 20px auto; /* Centrar el contenedor */
  }
  
  .experience-info {
    text-align: left;
  }
  
  .experience-info h2 {
    font-size: 2rem;
    color: #333;
    margin-bottom: 15px;
  }
  
  .experience-info p {
    font-size: 1.1rem;
    color: #555;
    margin-bottom: 10px;
  }
  
  .experience-info ul {
    list-style-type: disc;
    margin-left: 20px;
  }
  
  .experience-info ul li {
    font-size: 1rem;
    color: #444;
    margin-bottom: 5px;
  }
  