.about-me-container {
  display: flex; 
  align-items: center; 
  justify-content: space-between; 
  padding: 30px;
  background-color: #f9f9f9; 
  border-radius: 10px; 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
  max-width: 900px; 
  margin: 20px auto; 
  max-height: 300px; /* Establecer altura máxima */
  overflow: hidden; /* Ocultar el contenido desbordado */
}

.about-me-image {
  flex: 0 0 auto; 
  padding-right: 20px; 
}

.profile-pic {
  width: 100%; 
  max-width: 200px; 
  border-radius: 50%; 
  object-fit: cover; 
}

.about-me-text {
  flex: 1; 
  padding-left: 30px; 
  padding-right: 30px; 
}

.about-me-text h2 {
  margin-bottom: 10px; 
  font-size: 2rem; 
  color: #333;
}

.about-me-text p {
  font-size: 1rem; 
  line-height: 1.6; 
  color: #555;
  white-space: normal; 
  word-wrap: break-word; 
}
